@use "./PageSample.css" as *;

#navbar {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  width: 100%;
  height: 5rem;
  justify-content: center;
  font-size: 1.35rem;
  background-color: #100f11;
  z-index: 100;
}

#logoSection {
    opacity: 0;
    animation: logoLoading_anim 0.75s 1.5s linear forwards;
}

#homeOption {
    opacity: 0;
    animation: homeOptionLoading_anim 0.2s 2.25s linear forwards;
}

#skillOption {
    opacity: 0;
    animation: skillOptionLoading_anim 0.2s 2.45s linear forwards;
}

#projectsOption {
    opacity: 0;
    animation: projectsOptionLoading_anim 0.2s 2.65s linear forwards;
}

#aboutOption {
    opacity: 0;
    animation: aboutOptionLoading_anim 0.2s 2.85s linear forwards;
}

#contactOption {
    opacity: 0;
    animation: contactOptionLoading_anim 0.2s 3.05s linear forwards;
}

@keyframes logoLoading_anim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes homeOptionLoading_anim {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }

  @keyframes skillOptionLoading_anim {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }

  @keyframes projectsOptionLoading_anim {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }

  @keyframes aboutOptionLoading_anim {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }

  @keyframes contactOptionLoading_anim {
    0% {
      opacity: 0;
    }
  
    100% {
      opacity: 1;
    }
  }



#navbar-content {
  display: flex;
  align-items: center;
  width: 95%;
  height: 100%;

  justify-content: space-between;
}

#logoSection {
  padding: 0rem 2rem;
  font-size: 2rem;
  font-family: Inconsolata-Bold;
}

#navSelectionSection {
  display: flex;
  gap: 3rem;
  padding: 0rem 2rem;
  align-items: center;
}

#navOptions {
  display: flex;
  list-style: none;
  gap: 2rem;
}

#resumeButton {
  padding: 1rem 1.5rem;
  font-size: 1.35rem;
  opacity: 0;
  animation: resumeButton_anim 0.2s 3.25s linear forwards;
}

@keyframes resumeButton_anim {
   0% {
    opacity: 0;
   }
   100% {
    opacity: 1;
   }
}

#landingPageContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  //background-color: blue;
  width: 95%;
  height: 80%;
  align-content: center;
}

#checkOutProjectButton {
  display: flex;
  padding: 1.5rem 2rem;
  font-size: 1.5rem;
  opacity: 0;
  animation: checkprojectbutton_anim 0.25s 0.75s linear forwards;
}

@keyframes checkprojectbutton_anim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#welcomeMessage {
  display: flex;
  justify-content: center;
  //background-color: purple;
}

#welcomeMessageContent {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 90%;
  font-size: 2.35rem;
  justify-content: center;
}

#welcomeLine_1 {
  opacity: 0;
  animation: welcomeMessageAnim_1 0.25s linear forwards;
}

#welcomeLine_2 {
  opacity: 0;
  animation: welcomeMessageAnim_1 0.25s 0.25s linear forwards;
}

#welcomeLine_3 {
  opacity: 0;
  animation: welcomeMessageAnim_1 0.25s 0.5s linear forwards;
}

@keyframes welcomeMessageAnim_1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes welcomeMessageAnim_2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes welcomeMessageAnim_3 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#welcomeMessageContent p {
  font-size: 1.7rem;
}

#welcomeImage {
  display: flex;
  justify-content: center;
  //background-color: yellow;
  opacity: 0;
  animation: welcomeImage_anim 0.35s 1s linear forwards;
}

@keyframes welcomeImage_anim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

#welcomeImage img {
  max-width: 80%;
}

#githubSideButton {
transform: rotateZ(-60deg) translateY(-500%);
animation: sidebutton_anim 0.35s 3.25s linear forwards;
}

@keyframes sidebutton_anim {
    0% {
        transform: rotateZ(-60deg) translateY(-500%); 
    }

    100% {
        transform: rotateZ(0deg) translateY(0%);
    }
}

#mailSideButton {
    transform: rotateZ(-60deg) translateY(-500%);
    animation: sidebutton_anim 0.35s 3.6s linear forwards;
    
}

#linkedinSideButton {
    transform: rotateZ(-60deg) translateY(-500%);
    animation: sidebutton_anim 0.35s 3.95s linear forwards;
}

#leftSideline {
    transform: translateY(5rem) rotate(90deg);
    animation: leftsideline_anim 0.15s 4.15s linear forwards;
}

@keyframes leftsideline_anim {
    0% {
        transform: translateY(5rem) rotate(90deg);
    }

    100% {
        transform: translateY(0%) rotate(90deg);
    }
}

#sideEmail {
    transform: translateX(10rem);
    animation: sideEmail_anim 0.15s 4.45s linear forwards;
}

@keyframes sideEmail_anim {
    0% {
        transform: translateX(10rem);
    }

    100% {
        transform: translateX(0rem);
    }
}

#right_sideline {
    transform: translateY(10rem) rotate(90deg);
    animation: rightsideline_anim 0.15s 4.40s linear forwards;
}

@keyframes rightsideline_anim {
    0% {
        transform: translateY(10rem) rotate(90deg);
    }

    100% {
        transform: translateY(0rem) rotate(90deg);
    }
}